"use client";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import {
	Form,
	FormControl,
	FormField,
	FormItem,
	FormLabel,
	FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { useState } from "react";
import { useRouter } from "next/navigation";
import { api } from "@/services/api";
import Link from "next/link";
import { FaEye, FaEyeSlash, FaEnvelope, FaLock } from "react-icons/fa";
import Header from "@/components/Header";

const formSchema = z.object({
	email: z.string().email("E-mail inválido"),
	password: z
		.string()
		.min(8, "A senha deve ter pelo menos 8 caracteres")
		.regex(/[A-Z]/, "A senha deve conter pelo menos uma letra maiúscula")
		.regex(/[a-z]/, "A senha deve conter pelo menos uma letra minúscula")
		.regex(/[0-9]/, "A senha deve conter pelo menos um número"),
});

export default function Login() {
	const [isLoading, setIsLoading] = useState(false);
	const [showPassword, setShowPassword] = useState(false);
	const [loginError, setLoginError] = useState<string | null>(null);
	const router = useRouter();

	const form = useForm<z.infer<typeof formSchema>>({
		resolver: zodResolver(formSchema),
		defaultValues: { email: "", password: "" },
	});

	async function onSubmit(values: z.infer<typeof formSchema>) {
		setIsLoading(true);
		setLoginError(null);

		try {
			// Chamada à API de login
			const response = await api.auth.signin({
				email: values.email,
				password: values.password,
			});

			// Supondo que a API retorne um token
			if (response.accessToken) {
				// Armazenar o token (em localStorage, cookies ou contexto)
				localStorage.setItem("authToken", response.accessToken);

				// Redirecionar para a página inicial ou painel do usuário
				router.push("/");
			}
		} catch (error: any) {
			console.error("Erro no login:", error);
			setLoginError(
				error.message || "Credenciais inválidas. Por favor, tente novamente."
			);
		} finally {
			setIsLoading(false);
		}
	}

	return (
		<main className="min-h-screen flex flex-col">
			<div className="flex justify-center pt-24">
				<img
					src="https://in8.com.br/wp-content/themes/bootscore-child-main/img/logo.svg"
					alt="Logo da in8 holding" className="w-[200px] h-[100px]"
				/>
			</div>

			<div className="flex-1 flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
				<div className="max-w-md w-full space-y-8 bg-white p-8 rounded-xl shadow-md">
					<div className="text-center">
						<h2 className="mt-6 text-3xl font-bold text-gray-900">
							Entrar na sua conta
						</h2>
						<p className="mt-2 text-sm text-gray-600">
							Acesse sua conta para continuar
						</p>
					</div>

					{loginError && (
						<div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded-md">
							{loginError}
						</div>
					)}

					<Form {...form}>
						<form
							onSubmit={form.handleSubmit(onSubmit)}
							className="mt-8 space-y-6"
						>
							<FormField
								control={form.control}
								name="email"
								render={({ field }) => (
									<FormItem>
										<FormLabel>E-mail</FormLabel>
										<FormControl>
											<div className="relative">
												<div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none">
													<FaEnvelope className="text-gray-400" />
												</div>
												<Input
													placeholder="seuemail@exemplo.com"
													{...field}
													className="pl-10"
													disabled={isLoading}
													type="email"
												/>
											</div>
										</FormControl>
										<FormMessage />
									</FormItem>
								)}
							/>

							<FormField
								control={form.control}
								name="password"
								render={({ field }) => (
									<FormItem>
										<div className="flex justify-between items-center">
											<FormLabel>Senha</FormLabel>
											<Link
												href="/forgot-password"
												className="text-sm text-primary hover:underline"
											>
												Esqueceu a senha?
											</Link>
										</div>
										<FormControl>
											<div className="relative">
												<div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none">
													<FaLock className="text-gray-400" />
												</div>
												<Input
													placeholder="Digite sua senha"
													{...field}
													className="pl-10 pr-10"
													disabled={isLoading}
													type={showPassword ? "text" : "password"}
												/>
												<button
													type="button"
													className="absolute inset-y-0 right-0 flex items-center pr-3"
													onClick={() => setShowPassword(!showPassword)}
												>
													{showPassword ? (
														<FaEyeSlash className="text-gray-400" />
													) : (
														<FaEye className="text-gray-400" />
													)}
												</button>
											</div>
										</FormControl>
										<FormMessage />
									</FormItem>
								)}
							/>

							<div className="flex items-center">
								<input
									id="remember-me"
									name="remember-me"
									type="checkbox"
									className="h-4 w-4 text-primary focus:ring-primary border-gray-300 rounded"
								/>
								<label
									htmlFor="remember-me"
									className="ml-2 block text-sm text-gray-900"
								>
									Lembrar de mim
								</label>
							</div>

							<Button type="submit" className="w-full" disabled={isLoading}>
								{isLoading ? "Entrando..." : "Entrar"}
							</Button>

							<div className="text-center text-sm mt-4">
								<p className="text-gray-600">
									Não tem uma conta?{" "}
									<Link
										href="/auth/register"
										className="font-medium text-primary hover:underline"
									>
										Cadastre-se
									</Link>
								</p>
							</div>
						</form>
					</Form>
				</div>
			</div>
		</main>
	);
}
